.NavbarItems {
    background: rgb(255, 255, 255);
    height: 80px;
    display:flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;

}

.navbar-logo {
    color:rgba(0, 0, 0, 0.459);
    justify-self:start;
    position: relative;
    padding-left: 2rem;
    font-size: 26px;
    white-space: nowrap;
    
}

.nav-menu {
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 100%;
    margin-right: 2rem;
    justify-content: end;

}

.nav-links {
    color: rgba(0, 0, 0, 0.459);
    text-decoration: none;
    padding: 0.5rem, 2rem;
}

.nav-links:hover {
    background-color: rgba(0, 255, 255, 0.445);
    border-radius: 4px;
    transition: all 0.2s ease-out;
    padding: 3px 3px;
    color: white;
}

.fa-bars {
    color: rgba(0, 0, 0, 0.459);
}

.nav-links-mobile{
    display: none;
}

.menu-icon {
    display: none;
}

@media screen and (max-width: 960px) {
    .NavbarItems {
        position: relative;
    }
    .nav-menu{
       display: flex;
       flex-direction: column;
       justify-content: flex-start;
       width: 100%;
       height:  20vh;
       position: absolute;
       top: 80px;
       left: -100%;
       opacity: 1;
       transition: all 0.5s ease;
    }

    .nav-menu.active {
        background: rgba(0, 255, 255, 0.445);
        left: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-links{
        text-align: center;
        padding: 2rem;
        width: 100%;
        display: table;
        color: white;
    }

    .nav-links:hover {
        background-color: rgba(0, 0, 0, 0.466);
        border-radius:  0;
        
    }

    .navbar-logo {
        position: absolute;
        top: -1;
        left: 0;
        
    }

    .menu-icon {
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        transform: translate(-100%, 60%);
        font-size: 1.8rem;
        cursor: pointer;
    }

    .fa-times {
        color: rgba(0, 0, 0, 0.459);
        font-size:  2rem;

    }

    .nav-links-mobile {
        display: block;
        text-align: center;
        padding: 1.5rem;
        margin: 2rem auto;
        border-radius: 4px;
        width: 80%;
        background: rgba(0, 255, 255, 0.637);
        text-decoration: none;
        color: rgba(0, 0, 0, 0.459);
        font-size: 1.5rem;
    }

    .nav-links-mobile:hover {
        background: #fff;
        color: orange;
        transition: 250ms;
        

    }


}